.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid #0000001f;
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  resize: none;
}

.container {
  margin: 100px 50px 50px;
  display: flex;
}

.content {
  width: 155px;
  border: 1px solid #000;
  margin: 0 auto;
}

.item {
  width: 50px;
  height: 50px;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  user-select: none;
  border: 1px solid #000;
  font-size: 25px;
  line-height: 50px;
  transition: background .1s;
  display: inline-block;
}

.item:hover {
  background-color: #f1f1f10c;
}

